.header { height:68px; border-bottom:1px solid #e6e6e6; background-color:#fff; padding:0 20px;
}
.header .logo { float:left; display:block; height:36px; padding:16px 0;
}
.header .logo a { display:block; height:36px; color:#35495e; font-size:16px; font-weight:600; line-height:36px;
}
.header .logo img { float:left; display:block; width:36px; height:36px; margin-right:10px;
}
.header .nav-list { float:left; height:68px; margin-left:50px;
}
.header .nav { float:left; height:68px;
}
.header .nav a { display:block; height:68px; border-bottom:1px solid transparent; padding:0 15px; color:#666; font-size:16px; line-height:68px; -webkit-transition:all 0.2s; transition:all 0.2s;
}
.header .nav a:hover { border-bottom-color:#2d8cf0; color:#2d8cf0;
}
.header .nav a.active { border-bottom-color:#2d8cf0; color:#2d8cf0;
}
.header .algolia-search { float:left; width:140px;
}
.header .vui-button { float:left; margin-left:10px;
}

.body { display:-webkit-box; display:-ms-flexbox; display:flex; width:100%; min-height:calc(100vh - 164px);
}
.menu { position:relative; -webkit-box-sizing:border-box; box-sizing:border-box; width:250px; min-height:100%; -webkit-box-shadow:-1px 0 0 0 #e6e6e6 inset; box-shadow:-1px 0 0 0 #e6e6e6 inset;
}
.document { position:relative; -webkit-box-sizing:border-box; box-sizing:border-box; width:calc(100% - 250px); min-height:100%; padding:30px 180px 30px 30px;
}
.document .catalogue { position:absolute; top:30px; right:0; width:150px; font-size:12px; line-height:2;
}
.document .catalogue.affixed { position:fixed; top:30px; right:0;
}
.document .catalogue .catalogue-header { border-left:1px dashed #e6e6e6; padding:0 15px; font-weight:600;
}
.document .catalogue .catalogue-body { border-left:1px dashed #e6e6e6; padding:0 15px;
}
.document .catalogue .catalogue-body a { display:block; overflow:hidden; color:#595959; white-space:nowrap; text-overflow:ellipsis; -webkit-transition:all 0.2s; transition:all 0.2s;
